import { FC, useEffect, useState } from 'react';
import * as ST from './styled';
import {
  AppLoader,
  Avatar,
  Button,
  ButtonVariant, Comment,
  InfoItem,
  SideModal,
  Status,
  TextShorter,
  VideoPlayer
} from '@src/components';
import { UserStatus } from '@src/api/Users/models';
import { useModalControls } from '@src/hooks';
import { Dialog, DialogTypes } from '@components/Dialog';
import { toastError, toastSuccess } from '@src/utils/toast';
import { IPost, IPostDetail } from '@src/api/Posts/models';
import { PostsApi } from '@src/api/Posts/PostsApi';
import { Tag } from '@components/Tag';
import ReactPlayer from 'react-player';
import { getTime } from '@src/utils/date';
import { Icon, IconSize } from '@components/Icon';

interface IProps {
  postId: string;
  onClose: () => void;
  refetch: () => void;
}

export const PostModal: FC<IProps> = ({onClose, refetch, postId }) => {
  const [post, setPost] = useState<IPostDetail>();
  const [isLoading, setIsLoading] = useState(false);
  const banModal = useModalControls();

  const isActive = post?.status === UserStatus.ACTIVE;

  const fetchPost = async () => {
    try {
      const res = await PostsApi.fetchPostDetail(postId);
      setPost(res.data)
    } catch (e) {
      toastError(e);
    }
  }

  const handleBlockUnBlock = async () => {
    try {
      if (post) {
        setIsLoading(true);
        if (isActive) {
          const res = await PostsApi.banPost(post.id);
          toastSuccess(res.data.message);
        } else {
          const res = await PostsApi.unbanPost(post.id)
          toastSuccess(res.data.message);
        }
        refetch()
        banModal.close();
        onClose();
      }
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  useEffect(() => {
    fetchPost();
  }, [postId]);

  return (
    <SideModal
      titleText={post && (
        <ST.Header>
          <ST.Title>
            <TextShorter tooltip>
              {post.title} ({post.id})
            </TextShorter>
          </ST.Title>
          <Status status={post.status} />
          <ST.InfoItem>
            <Icon type="eye" size={IconSize.LG} />
            {post.views}
          </ST.InfoItem>
          <ST.InfoItem>
            <Icon type="star-filled" size={IconSize.LG} />
            {post.favoriteCount}
          </ST.InfoItem>
          <ST.InfoItem>
            <Icon type="message" size={IconSize.LG} />
            {post.commentsCount}
          </ST.InfoItem>
        </ST.Header>
      )}
      onClose={onClose}
      closeOnDocumentClick={false}
    >
      <ST.Wrapper>
        {post ? (
          <>
            <InfoItem
              text="ID:"
              info={(
                <>
                  {post.id}
                </>
              )}
            />
            <InfoItem
              text="Заголовок:"
              info={(
                <>
                  {post.title}
                </>
              )}
            />
            <InfoItem
              text="Автор:"
              info={(
                <>
                  <Avatar
                    firstName={post.user.firstName}
                    lastName={post.user.lastName}
                    src={post.user.avatar}
                  />
                  {post.user.lastName} {post.user.firstName} ({post.user.email})
                </>
              )}
            />
            <InfoItem
              text="Создан:"
              info={getTime(post?.createdAt)}
            />
            <InfoItem
              text="Обновлен:"
              info={getTime(post?.updatedAt)}
            />
            <InfoItem
              text="Теги:"
              info={(
                <ST.Info>
                  {post.tags.map(it => <Tag key={it.id} tag={it} />)}
                </ST.Info>
              )}
            />
            <InfoItem
              text="Краткое описание:"
              info={(
                <ST.Description>
                  {post.shortDescription}
                </ST.Description>
              )}
            />
            <InfoItem
              text="Описание:"
              info={(
                <ST.Description>
                  {post.description}
                </ST.Description>
              )}
            />
            <VideoPlayer
              url={post.videoUrl}
            />
            <ST.Comments>
              <ST.CommentsText>
                Комментарии
              </ST.CommentsText>
              {post.comments.map(it => <Comment key={it.id} comment={it} />)}
            </ST.Comments>
            <ST.Footer>
              <Button
                fullWidth
                onClick={banModal.open}
                variant={isActive ? ButtonVariant.DANGER : ButtonVariant.FILLED}
                text={isActive ? 'Заблокировать' : 'Разблокировать'}
              />
            </ST.Footer>
          </>
        ) : (
          <AppLoader />
        )}
      </ST.Wrapper>
      {banModal.isOpen && (
        <Dialog
          type={isActive ? DialogTypes.WARNING : DialogTypes.ANSWER}
          closeHandler={banModal.close}
          onConfirm={handleBlockUnBlock}
          actions={{
            cancel: {
              text: 'Отменить',
              onClick: banModal.close,
            },
            submit: {
              isLoading,
              text: isActive ? 'Заблокировать' : 'Разблокировать',
            }
          }}
        >
          Вы действительно хотите {isActive ? 'заблокировать' : 'разблокировать'} пост "{post?.title}"?
        </Dialog>
      )}
    </SideModal>
  )
}
