import { FC } from 'react';
import { IPost } from '@src/api/Posts/models';
import * as ST from './styled';
import { Tag } from '@components/Tag';
import { Avatar, Status } from '@src/components';
import { Icon, IconSize } from '@components/Icon';

interface IProps {
  post: IPost;
  onClick: () => void;
}

export const PostItem: FC<IProps> = ({ post, onClick }) => {
  return (
    <ST.Wrapper onClick={onClick}>
      <ST.Header>
        <ST.Title>
          {post.title} ({post.id})
        </ST.Title>
        <ST.Info>
          <ST.InfoItem>
            <Icon type="eye" size={IconSize.LG} />
            {post.views}
          </ST.InfoItem>
          <ST.InfoItem>
            <Icon type="star-filled" size={IconSize.LG} />
            {post.favoriteCount}
          </ST.InfoItem>
          <ST.InfoItem>
            <Icon type="message" size={IconSize.LG} />
            {post.commentsCount}
          </ST.InfoItem>
          <Status status={post.status} />
        </ST.Info>
      </ST.Header>
      <ST.Info>
        {post.tags.map(it => <Tag key={it.id} tag={it} />)}
      </ST.Info>
      <ST.Info>
        <Avatar
          firstName={post.user.firstName}
          lastName={post.user.lastName}
          src={post.user.avatar}
        />
        {post.user.lastName} {post.user.firstName} ({post.user.email})
      </ST.Info>
      <ST.Description>{post.shortDescription}</ST.Description>
    </ST.Wrapper>
  );
}
