import { useFormik } from 'formik';
import { useDebounce, useTags, useUsers } from '@src/hooks';
import { ChangeEventHandler, useEffect, useMemo, useState } from 'react';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { CmSelectValue } from '@src/components';
import { PostsApi } from '@src/api/Posts/PostsApi';
import { toastError } from '@src/utils/toast';
import { IPost } from '@src/api/Posts/models';
import { UserStatus } from '@src/api/Users/models';

export interface IFilterPostsForm {
  tags: SelectOption<string>[];
  users: SelectOption<string>[];
  status: SelectOption<UserStatus>[];
}

export const useFilters = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [posts, setPosts] = useState<IPost[]>([]);
  const [search, setSearch] = useState('');
  const debouncedSearch = useDebounce(search, 400)

  const form = useFormik<IFilterPostsForm>({
    initialValues: {
      tags: [],
      users: [],
      status: [],
    },

    onSubmit: () => {}
  })

  const usersRes = useUsers();
  const tagsRes = useTags();

  useEffect(() => {
    usersRes.getUsers();
    tagsRes.getTags();
  }, []);

  useEffect(() => {
    getPosts()
  }, [form.values.users, form.values.tags, debouncedSearch, form.values.status]);

  const handleChangeSearch: ChangeEventHandler<HTMLInputElement> = (e) => {
    setSearch(e.target.value);
  }

  const getPosts = async () => {
    try {
      setIsLoading(true)
      const { users, tags, status } = form.values;
      const res = await PostsApi.fetchPosts({ search: debouncedSearch, tags: tags.map(it => it.value), users: users.map(it => it.value), status: status.map(it => it.value) });
      setPosts(res.data);
    } catch (e) {
      toastError(e)
    } finally {
      setIsLoading(false)
    }
  }

  const usersOptions: SelectOption<string>[] = useMemo(() => {
    return usersRes.users.map(it => ({ value: it.id, label: it.email, customize: { avatar: `${it.lastName} ${it.firstName}` } }))
  }, [usersRes.users]);

  const tagsOptions: SelectOption<string>[] = useMemo(() => {
    return tagsRes.tags.map(it => ({ value: it.id, label: it.value }))
  }, [tagsRes.tags]);

  const statusOptions: SelectOption<UserStatus>[] = useMemo(() => {
    return Object.values(UserStatus).map(it => ({ value: it, label: it }))
  }, []);

  const createSelectChangeHandler = (field: string) => (value: CmSelectValue) => {
    form.setFieldValue(field, value);
  }

  return {
    form,
    usersOptions,
    tagsOptions,
    usersRes,
    tagsRes,
    posts,
    statusOptions,
    createSelectChangeHandler,
    handleChangeSearch,
    search,
    isLoading,
    getPosts,
  }
}
