import {  FC, useMemo, useState } from 'react';
import * as ST from './styled';
import { AppLoader, PageHeader, Select } from '@src/components';
import { IPost } from '@src/api/Posts/models';
import { PostItem } from '@pages/Posts/PostItem';
import { PostModal } from '@pages/Posts/PostModal';
import { useFilters } from '@pages/Posts/useFilters';

const Posts: FC = () => {
  const [activePost, setActivePost] = useState<IPost>();
  const {
    form,
    tagsRes,
    tagsOptions,
    usersOptions,
    usersRes,
    createSelectChangeHandler,
    posts,
    isLoading,
    search,
    handleChangeSearch,
    getPosts,
    statusOptions,
  } = useFilters();

  const renderedPosts = useMemo(() => {
    if (isLoading) {
      return (
        <AppLoader />
      )
    }

    if (!posts.length) {
      return (
        <ST.EmptyText>
          Постов нету
        </ST.EmptyText>
      );
    }

    return (
      <ST.Content>
        {posts.map(it => <PostItem key={it.id} post={it} onClick={() => setActivePost(it)} />)}
      </ST.Content>
    )
  }, [posts, isLoading]);

  return (
    <ST.Wrapper>
      <PageHeader title="Посты" search={search} onChangeSearch={handleChangeSearch} />
      <ST.Filters>
        <Select value={form.values.tags} options={tagsOptions} loading={tagsRes.isLoading} onChange={createSelectChangeHandler('tags')} placeholder="Выберите теги" multi searchable />
        <Select value={form.values.users} options={usersOptions} loading={usersRes.isLoading} onChange={createSelectChangeHandler('users')} placeholder="Выберите пользователей" multi searchable />
        <Select value={form.values.status} options={statusOptions} onChange={createSelectChangeHandler('status')} placeholder="Выберите статусы" multi />
      </ST.Filters>
      {renderedPosts}
      {activePost && <PostModal postId={activePost.id} onClose={() => setActivePost(undefined)} refetch={getPosts} />}
    </ST.Wrapper>
  )
}

export default Posts;
